<template>
  <div class="lottery-wheel">
    <div class="wheel-table">
      <div class="wheel-pointer">
        <div @click="lotteryStart" class="lottery-button">
          <span>Go</span><span>{{ restCount }}</span>
        </div>
      </div>
      <div
        class="wheel-dial"
        :style="{ transform: rotateAngle, transition: rotateTransition }"
      ></div>
    </div>
    <el-dialog
      title=""
      :visible.sync="successDialogVisible"
      :append-to-body="true"
      top="40vh"
      custom-class="lottery-success-dialog"
      width="410px"
    >
      <div class="lottery-success">
        <div class="lottery-success-content">
          <i class="icon-smile"></i>
          <div>
            <p>{{ successWord }}</p>
            <p>
              1
            </p>
          </div>
        </div>
        <div class="lottery-success-btn">
          <el-button
            size="small"
            type="primary"
            @click="successDialogVisible = false"
            > </el-button
          >
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      successWord: "",
      successDialogVisible: false,
      hasPrize: false, // 
      startRotatingDegree: 0, // 
      rotateAngle: 0, // 
      rotateTransition: "transform 6s ease-in-out", // 
      clickFlag: true, // 
      prizeList: [
        {
          id: 1,
          name: "8", // 
          angle: 45,
          isPrize: 1 // 
        },
        {
          id: 2,
          name: "2000", // 
          angle: 15,
          isPrize: 1 // 
        },
        {
          id: 3,
          name: "1000", // 
          angle: 315,
          isPrize: 1 // 
        },
        {
          id: 4,
          name: "500", // 
          angle: 285,
          isPrize: 1 // 
        },
        {
          id: 5,
          name: "500GLOGO", // 
          angle: 225,
          isPrize: 1 // 
        },
        {
          id: 6,
          name: "", // 
          angle: 165,
          isPrize: 1 // 
        },
        {
          id: 7,
          name: "16GU", // 
          angle: 135,
          isPrize: 1 // 
        },
        {
          id: 8,
          name: "", // 
          angle: 345,
          isPrize: 1 // 
        },
        {
          id: 9,
          name: "", // 
          angle: 255,
          isPrize: 1 // 
        },
        {
          id: 10,
          name: "", // 
          angle: 75,
          isPrize: 1 // 
        },
        {
          id: 11,
          name: "", // 
          angle: 105,
          isPrize: 1 // 
        },
        {
          id: 12,
          name: "A5", // 
          angle: 195,
          isPrize: 1 // 
        }
      ],
      currentHref: "",
      restCount: 10
    };
  },
  mounted() {
    this.currentHref = window.location.href;
  },
  methods: {
    lotteryStart() {
      this.lotteryByApi();
    },
    lotteryByApi() {
      //  
      if (!this.clickFlag) {
        return;
      }
      //  
      if (this.restCount <= 0) {
        const _this = this;
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            _this.$scrollTo(document.getElementById("J-nav-csgj"), 500, {
              offset: -100
            });
          })
          .catch(() => {});
        return;
      }

      const _this = this;
      this.mockLotteryApi()
        .then(res => {
          if (res.success) {
            const data = res.data;
            const type = data.resultType;
            if (type === 1) {
              _this.$alert("", "", {
                type: "warning"
              });
            } else if (type === 2) {
              // 
              _this.rotating(data);
              _this.restCount--;
            } else if (type === 3) {
              _this.$alert("", "", {
                type: "warning"
              });
            } else if (type === 4) {
              _this.$alert("", "", {
                type: "warning"
              });
            } else {
              _this.$alert("", "", {
                type: "warning"
              });
            }
          } else {
            _this.$alert("Sorry", "", {
              type: "warning"
            });
          }
        })
        .catch(() => {
          _this.$alert("Sorry", "", {
            type: "warning"
          });
        });
    },
    /**
     * 
     * @param currentPrize//
     */
    rotating(currentPrize) {
      const duringTime = 5; // 1s
      const resultIndex = currentPrize.rewardId; // prizeListid
      const randCircle = 8; // 2-3
      this.clickFlag = false; // 
      const prize = this.prizeList.find(prize => prize.id === resultIndex);
      const rotateAngle =
        this.startRotatingDegree +
        randCircle * 360 +
        prize.angle -
        (this.startRotatingDegree % 360);
      this.startRotatingDegree = rotateAngle;
      this.rotateAngle = `rotate(${rotateAngle}deg)`;
      const _this = this;
      // 
      setTimeout(() => {
        _this.clickFlag = true;
        _this.gameOver(prize);
      }, duringTime * 1000 + 1500); // 
    },
    gameOver(prize) {
      if (prize) {
        this.successWord = `${prize.name}`;
        this.successDialogVisible = true;
      } else {
        this.$alert("");
      }
    },
    mockLotteryApi() {
      return Promise.resolve({
        success: true,
        data: {
          resultType: 2,
          rewardId: Math.floor(Math.random() * 13)
        }
      });
    }
  }
};
</script>
